body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Resizer.horizontal{
  background-color: rgb(25, 25, 25)!important;
  border: 2px solid black;
  border-left: none;
  border-right: none;
  height: 1px!important;
  cursor: row-resize!important;
}

.Pane.horizontal.Pane1{
  max-height: 80%; 
}
.Pane.horizontal.Pane2{
  overflow: auto!important;
}
.CodeMirror{
  height: 100%!important;
  color: #fff!important;
}
.controlled-editor{
  height: 100%;
}

.cm-line{
  color: #fff;
  font-size: 15px;
}

.cm-activeLine {
  border: 0.1px solid rgba(128, 128, 128, 0.181);
}

.cm-gutters{
  background-color: #1E1E1E!important;
  color: white!important;
  font-size: 15px;
  width: 30px;
  border: none!important;
}
.cm-activeLineGutter{
  background-color: #1E1E1E!important;
  color: white;
}
.cm-activeLine,.controlled-editor{
  background-color: #1E1E1E!important;
}
.cm-editor{
  height: 100%;
  background-color: #1E1E1E;
}
.CodeMirror-scroll{
  background-color: #1E1E1E;
  color: white;
}

.MuiMenuItem-gutters{
  font-size: 12px!important;
  word-wrap: break-word!important;
}

::-webkit-scrollbar{
  background-color: #1E1E1E;
  border: 0.1px solid rgba(128, 128, 128, 0.181);
  width: 15px;
}
::-webkit-scrollbar-thumb{
  background-color: #3b3b3b;
}

.MuiMenu-paper::-webkit-scrollbar{
  width: 8px!important;
  background-color: #777777
}


.MuiBackdrop-root.MuiModal-backdrop.css-yiavyu-MuiBackdrop-root-MuiDialog-backdrop{
  background-color: #000000d1;
}
