.cont{
    background-color: black;
    width: inherit;
    height: 99vh;
    position: relative;
}

.first{
    height: inherit;
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: hidden;
}
.zoomFirst{
    height: 90vh;
    z-index: 9;
}

.second{
    height: 100%;
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow:hidden;
}
.zoomSec{
    height: 90vh;
    position: absolute;
    top: 0;
    z-index: 9;
}
.editor{
    background-color: #1E1E1E;
    flex-grow: 1;
    overflow-x: auto;
}

.output{
    background-color: #1E1E1E;
    flex-grow: 1;
    color: white;
    padding: 20px;
    font-size: 15px;
    white-space: pre;
    overflow-x: auto;
}
.input{
    background-color: #1E1E1E;
    flex-grow: 1;
    color: white;
    padding: 15px;
    font-size: 15px;
    white-space: pre;
    overflow-x: auto;
}
.input textarea{
    width: 100%;
    height: 90%;
    font-size: 15px;
    border: none;
}
.title{
    background-color: black;
    display: flex;
    justify-content: space-between;
}
.titles{
    display: flex;
}
.title .section{
    background-color: #1E1E1E;
    color: white;
    padding: 0px 25px 0 14px;
    margin-top: 3px;
    margin-right: 3px;
    cursor: pointer;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.noActive{
    background-color: #1e1e1e71!important;
    color: rgba(255, 255, 255, 0.338)!important;
}
.noActive:hover{
    background-color: #1E1E1E!important;
    color: white!important;
}