.mainHeader{
    height: 3rem;
    display: flex;
    align-items: center;
    position: static;
    background-color: #1E1E1E;
}
.run_btn{
    background-color: #748FFC;
    border-radius: 10px;
    display: flex;
    align-items: center;
    width: 8rem;
    height: 70%;
    border-radius: 8px;
    box-shadow: 0px 6px 12px 0px rgba(255, 255, 255, 0.208), 0px 2px 24px 0px rgba(208, 208, 208, 0.301);
}
.end{
    position: absolute;
    right: 0;
    height: 100%;
}

.settingsOption{
    display: flex;
    color: #1E1E1E;
    font-size: 13px;
    padding: 15px 0;
}
.settingsOption .headingO{
    margin: 0px 15px 0px 0;
    font-weight: 600;
}
.settingsOption .option{
    display: flex;
    align-items: center;
    color: black;
    text-align: center;
    margin: 0 10px;
}
.settingsOption .option input{
    margin: 0 7px 0 0!important;
    cursor: pointer;
}

input[type='radio']:after {
    width: 15px;
    height: 15px;
    border-radius: 15px;
    top: -2px;
    left: -1px;
    position: relative;
    background-color: #d1d3d1;
    content: '';
    display: inline-block;
    visibility: visible;
    border: 2px solid white;
}

input[type='radio']:checked:after {
    position: relative;
    background-color: #1E1E1E;
}

